import React, { useState } from 'react';
import { Button, Input, Toast, Form } from 'antd-mobile';
import axios from '../utils/axios'; //  axios 
import CryptoJS from 'crypto-js';
import { useNavigate } from 'react-router-dom';
const LoginPage: React.FC = () => {
  const [account, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate(); // 
  const handleLogin = async () => {
    // 
    if (!account || !password) {
      Toast.show('');
      return;
    }

    setLoading(true);
    const encryptedPassword = CryptoJS.MD5(password).toString(); //  Base64 

    try {
      //  API 
      const response = await axios.post('/api/sysUser/login', { account, password:encryptedPassword,type:1      });
      if (response.data.code===200) {
        const token = response.data.data.token;  //  token  response.data.token 
        localStorage.setItem('token', token);  //  localStorage
        Toast.show('');
        // 
        navigate('/');
        // 
      } else {
        Toast.show('');
      }
    } catch (error) {
      Toast.show('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ padding: '20px', backgroundColor: '#fff' }}>
      <h2 style={{ textAlign: 'center', marginBottom: '20px' }}></h2>

      <Form layout="horizontal" onFinish={handleLogin}>
        <Form.Item label="" name="account">
          <Input
            value={account}
            onChange={(e) => setUsername(e)}
            placeholder=""
          />
        </Form.Item>

        <Form.Item label="" name="password">
          <Input
            type="password"
            value={password}
            onChange={(e) => setPassword(e)}
            placeholder=""
          />
        </Form.Item>

        <Form.Item>
          <Button
            color="primary"
            block
            onClick={handleLogin}
            loading={loading}
          >
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default LoginPage;
